<template>
  <div  :class="['AvatarPicker', shown?'':'hidden']">
    <vue-cropper  ref="cropper"  :src="startImg"  alt="Source Image" class="cropper"
                 :crop="cropIt"
                 :background="false" :modal="true" :toggleDragModeOnDblclick="false"
                 :cropBoxMovable="false" :highlight="false" :guides="false" :center="false" :cropBoxResizable="false"
                 :aspectRatio="1" :viewMode="1" dragMode="move">
    </vue-cropper>
    <div class="btn" @click="$refs.img.click();">Выбрать изображение</div><input class='hidden' type="file" ref="img" :file="loadedFile" @change="updateImg">
    <div class="btn" @click="pickAvatar">Сохранить</div>
    <div class="btn" @click="shown=false">Отмена</div>
    <span><img :src="cropImage" class="big"/></span>
    <span><img :src="cropImage" class="small"/></span>
  </div>
</template>

<script>
  import VueCropper from 'vue-cropperjs';
  import 'cropperjs/dist/cropper.css';

  export default {
    name: "AvatarPicker",
    props:['startImg'],
    components: {VueCropper},
    data: function () {
      return{
        shown:false,
        scrollDisabled:false,
        cropImage:'',
        cropOptions:{viewMode:1},
        loadedFile:'',

      }
    },
    methods:{
      test:function(){
        this.cropIt();
        console.log(this.$refs.cropper.getData());
      },
      showPicker:function(){
        this.shown = true;
        this.$refs.cropper.replace(this.loadedFile);
      },
      cropIt:function () {
        //console.warn('CROP');
        this.cropImage = this.$refs.cropper.getCroppedCanvas().toDataURL();
        //this.resizeImgs();
      },
      updateImg :function(event){
        var vm = this;
        var input = event.target;

        if (input.files){
          //console.log(input.files);
          var reader = new FileReader();
          reader.onloadend = function (e) {
            vm.$refs.cropper.replace(e.target.result);
          }
          //console.log(reader);
          //console.log(reader.result);
          reader.readAsDataURL(input.files[0]);
          //
          //vm.$refs.cropper.reset();
        }
      },
      resizeImgs: function () {
        const vm = this;
        const fr = new FileReader();
        const fr2 = new FileReader();
        const img = new Image();
        img.src = vm.cropImage;
        img.onload = () =>{
          const elem = document.createElement('canvas');
          const ctx = elem.getContext('2d');

          elem.height = vm.smallsize;
          elem.width = vm.smallsize;
          ctx.drawImage(img,0,0,vm.smallsize,vm.smallsize);
          vm.smallImg = elem.toDataURL('image/png');
          ctx.canvas.toBlob((blob)=>{let xf = new File([blob],'XX',{type:'image/png', lastModified: Date.now()});
            vm.smallImg= xf; vm.bigImg= xf;}, 'image/png', 1);

          elem.height = vm.bigsize;
          elem.width = vm.bigsize;
          ctx.drawImage(img,0,0,vm.bigsize,vm.bigsize);
          vm.bigImg = elem.toDataURL('image/png');


          let xfile = new File();
          xfile.name = 'XX';
          xfile.type = 'image/png';
          xfile.lastModified=Date.now();
          console.log(xfile);
        };
      },
      pickAvatar: function () {
        this.$emit('pickAvatar',this.cropImage);
        //this.shown = false;
      }
    },
    computed:{
    },
  }
</script>

<style lang="sass" scoped>
  $lgray: #f6f6f6
  $myblue: #182b93
  $myred: #CA1612
  $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
  .AvatarPicker
    width: 300px
    height: 500px
    background-color: aquamarine
    margin: 30px
    position: absolute
    .cropper
      width: 220px
      height: 220px
      border: black solid 1px
      overflow: hidden
    .big
      width: 170px
      height: 170px
    .small
      width: 71px
      height: 71px
    .btn
      text-align: center
      display: block
      padding: 8px 5px 8px 10px
      background-color: white
      box-shadow: $shadow
      width: 150px
      font-family: Philosopher
      margin: 5px

</style>



